import React from 'react';
import styles from './Lead.module.scss';
import author from '../../assets/images/lead-author.png';
import { BreadCrumbs, CTA, SpeakerCard } from '../../components';
import { breadCrumbsItems } from '../../utils/breadCrumbsItems';
import clsx from 'clsx';

export const Lead = () => {
  return (
    <section className={styles.root}>
      <div className={styles.container}>
        <BreadCrumbs pathNames={breadCrumbsItems} />
        <div className={styles.dateContainer}>
          <span className={styles.date}>8-9  2022</span>
          <CTA path='https://cbo.ru/' type='lead' linkText='-' />
        </div>
        <h1 className={styles.title}>
              : <span className={styles.titleSpan}></span>{' '}
          <span className={clsx(styles.titleSpan, styles.titleSpanTypeLast)}>&nbsp; </span>
        </h1>
        <p className={styles.subtitle}>  ,    ?</p>
        <CTA path='#' linkText=' ' />
        <div className={styles.imageWrapper}>
          <img
            src={author}
            alt=' ,  -'
            className={styles.authorImage}
            loading='lazy'
          />
          <SpeakerCard />
        </div>
      </div>
    </section>
  );
};
